@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app .app-left{position:fixed}.app .app-left .navigation{font-family:'Fira Sans', sans-serif}.app .app-left .navigation ul li{list-style:none}.app .app-left .navigation ul li a{text-decoration:none;color:#000;padding:10px;display:inline-block;font-weight:bold;font-size:20px}.app .app-right{margin-left:300px}.app .app-right .about{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;font-family:'Fira Sans', sans-serif}.app .app-right .about .about-left{width:50%}.app .app-right .about .about-left h2{font-size:40px}.app .app-right .about .about-left p{font-size:20px;line-height:30px}.app .app-right .about .about-right{width:50%;padding:50px}.app .app-right .about .about-right img{border-radius:50%;width:400px;height:400px}.app .app-right .projects{font-family:'Fira Sans', sans-serif}.app .app-right .projects .project .project1 a{text-decoration:none;color:#000}.app .app-right .projects .project .project1 img{width:50%;height:50%}.app .app-right .projects .project .project2 a{text-decoration:none;color:#000}.app .app-right .projects .project .project2 img{width:50%;height:50%}.app .app-right .projects .project .project3 a{text-decoration:none;color:#000}.app .app-right .projects .project .project3 img{width:50%;height:50%}

